
.form {
    position: relative;
    z-index: 1;
    background: #FFFFFF;
    max-width: 350px;
    margin: 0 auto 100px;
    padding: 30px;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;
    text-align: center;
}

.form .thumbnail {
    background: #d218cf;
    width: 150px;
    height: 150px;
    margin: 0 auto 30px;
    padding: 50px 30px;
    border-top-left-radius: 100%;
    border-top-right-radius: 100%;
    border-bottom-left-radius: 100%;
    border-bottom-right-radius: 100%;
    box-sizing: border-box;
}

.form .thumbnail i {
    display: block;
    width: 100%;
    position: relative;
    top: -14px;
}

.form input {
    outline: 0;
    background: #f2f2f2;
    width: 100%;
    border: 0;
    margin: 0 0 15px;
    padding: 15px;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;
    box-sizing: border-box;
    font-size: 14px;
    text-align: center;
}


.form .message {
    margin: 15px 0 0;
    color: #972121;
    font-size: 12px;
}

.container {
    position: relative;
    z-index: 1;
    margin: 0 auto;
}

.container:before,
.container:after {
    content: "";
    display: block;
    clear: both;
}

.container .info {
    margin: 50px auto;
    text-align: center;
    
}

.container .info h1 {
    margin: 0 0 15px;
    padding: 0;
    font-size: 30px;
    font-weight: 300;
    color: #1a1a1a;
    white-space: pre-wrap;
}

.container .info span {
    color: #4d4d4d;
    font-size: 12px;
    
}

.container .info span a {
    color: #000000;
    text-decoration: none;
    font-size: 20px;
}


.container .info span .fa {
    color: #EF3B3A;
}

body {
    
    font-family: "Roboto", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body:before {
   
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(255, 255, 255, 0.8);
    width: 100%;
    height: 100%;
}

.logo {
    position: absolute;
    top: 20px;
    left: 20px;
}

.logo img {
    width: 100px;
    height: 100px;
}
 